

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App{
    min-height: 100vh;
    background-image: linear-gradient(to top, rgba(0,0,0,0.9), rgba(0,0,0,0)),
    url('./image/bg.jpg');
    background-size: cover;
    display: flex;
    justify-content: center;
}

.main{
    width: 500px;
    /* position: relative;
    top: 25%;
    left: 50%;
    transform: translateX(-50%); */
    margin: 20% 0;
   
}

.form-input{
    display: flex;
    justify-content: space-evenly;
    background-color: #fff;
    padding: 16px 0;
    border-radius: 12px 12px 8px 8px;
    border-bottom: 4px solid #52AD7C;
    transition: all 0.3s;
}

.job-input{
    border: none;
    outline: none;
    padding: 8px 10px;
    font-size: 28px;
}

.add-btn{
    border: none;
    outline: none;
    font-size: 20px;
    padding: 8px 26px;
    color: #fff;
    border-radius: 4px;
    background-color: #058e44;
    opacity: 0.8;
}

.add-btn:hover{
    cursor: pointer;
    opacity: 1;
}

.list-job{
    width: 100%;
    background-color: #fff;
    border-radius: 0 0 12px 12px;
    font-size: 24px;
    transition: all 0.3s;
}

.list-job li{
    padding: 16px;
    border-top: 0.5px solid #C3BB8A;
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 24px;
    cursor: pointer;
}

.list-job li p{
    max-width: 90%;
    word-wrap: break-word;
}

.list-job li i{
    display: none;
}

.list-job li:hover i{
    display: block;
    color: #da330e;
}

.finish p{
    text-decoration: line-through;
    color: #9B9B9B;
}

@media only screen and (max-width: 768px){

    .main{
        width: 350px;
    }

    .job-input{
        font-size: 20px;
    }

    .add-btn{
        padding: 8px;
    }

}